/*----------------------------------
6 - PromoBox
----------------------------------*/

.promobox-i {
	margin: 0 0 30px;
	border: 1px solid #e0e4f6;
	padding: 31px 40px 31px 40px;
	position: relative;
	background: #fff;
	color: #686f8a;
	font-size: 16px;
	border-radius: 3px;
	text-align: center;
	line-height: 2;
}
.promobox-i.promobox-i-hasimg {
	min-height: 100px;
	padding-left: 172px;
}
.promobox-i.promobox-i-hasimg,
.promobox-i.promobox-i-hasbtn {
	text-align: left;
	padding-top: 22px;
	padding-bottom: 16px;
}
.promobox-i.promobox-i-hasbtn {
	padding-right: 280px;
}
.promobox-i.promobox-i-hasbtn:not(.promobox-i-hasimg):after {
	content: '';
	display: block;
	width: 8px;
	top: -1px;
	bottom: -1px;
	left: 0;
	background: #283346;
	border-radius: 3px 0 0 3px;
	position: absolute;
}
.promobox-i h3 {
	font-weight: 700;
	margin: 3px 0;
	padding: 0;
	font-size: 25px;
	line-height: 1.42857143;
}
.promobox-i.promobox-i-hasimg h3,
.promobox-i.promobox-i-hasbtn h3 {
	font-size: 19px;
}
.promobox-i p {
	margin: 3px 0;
	line-height: 2;
}
.promobox-i .promobox-i-link {
	background: #6bc67c;
	position: absolute;
	text-align: center;
	top: 50%;
	right: 49px;
	border-radius: 2px;
	padding: 0 15px;
	margin: -20px 0 0;
	height: 40px;
	line-height: 40px;
	transition: all .2s;
	color: #fff;
	font-size: 14px;
	font-weight: 700;
	min-width: 174px;
	display: inline-block;
	text-transform: uppercase;
}
.promobox-i .promobox-i-link:hover {
	background: #90d49d;
}
.promobox-i .promobox-i-img {
	margin: -43px 0 0;
	padding: 0;
	position: absolute;
	width: 86px;
	height: 86px;
	border-radius: 86px;
	overflow: hidden;
	position: absolute;
	top: 50%;
	left: 45px;
}
.promobox-i .promobox-i-img img {
	display: block;
	width: 100%;
	height: 100%;
}
.promobox-i .promobox-i-icon {
	margin: -43px 0 0;
	padding: 0;
	position: absolute;
	width: 86px;
	height: 86px;
	line-height: 86px;
	border-radius: 86px;
	overflow: hidden;
	position: absolute;
	top: 50%;
	left: 45px;
	background: #6bc67c;
	text-align: center;
}
.promobox-i .promobox-i-icon i {
	color: #fff;
	font-size: 30px;
	line-height: 100%;
	vertical-align: middle;
}

